ks-order-login-prompt {
    display: block;
    text-align: left;
}

ks-order-login-prompt .content {
    width: 100%;
}

ks-order-login-prompt .wrapper h1 {
    font-weight: 700;
}

ks-order-login-prompt .buttons {
    margin-top: 30px;
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}

ks-order-login-prompt .buttons > * {
    flex: 1 1 auto;
}

@media (max-width: 720px) {
    ks-order-login-prompt {
        text-align: center;
    }
}