import { Component, h, Element, Prop, Method } from '@stencil/core';

@Component({
	tag: 'ks-order-login-prompt',
    styleUrl: 'orderLoginPrompt.css'
})
export class OrderLoginPrompt {
    @Element() root: HTMLElement;
    @Prop({mutable: true}) message: string = "";
    @Prop() heading: string = "";
    @Prop() toCart: string = "";
    @Prop() toConfirmation: string = "";

    @Method()
    async show(message?: string) {
        if(message) this.message = message;
        this.root.querySelector("ks-dialog").show();
    }

    ToCart() {
        window.location.href = 'koszyk.html';
    }

    @Method()
    async ToConfirmation() {
        window.location.href = 'zamowienie-potwierdzenie.html';
    }

	render() {
		return <ks-dialog dark closable={false}>
            <div class="wrapper">
                <h1>{this.heading}</h1>
                {this.message && <p>{this.message}</p>}
                <div class="buttons">
                    <ks-button round name={this.toCart} onClick={()=>this.ToCart()} />
                    <ks-button round secondary name={this.toConfirmation} onClick={()=>this.ToConfirmation()} />
                </div>
            </div>
        </ks-dialog>
    }

}
